import api from '../../common/request'
import { mapState } from 'vuex'
	
export default {
	data() {
		return {
			userId: '',
			currentTab: 0,
			booklist: [],
			showCheck: true,
			readlist: []
		}
	},
	onLoad() {
		// if (this.userId === '') {
		//   this.userId = uni.getStorageSync('__userid__') || ''
		// }
		// this.getBookListData()
		// this.getReadList()
	},
	onReady() {
		
	},
	onShow() {
		if (this.userId === '') {
			this.userId = this.userid ? this.userid : uni.getStorageSync('__userid__')
		}
		this.getBookListData()
		this.getReadList()
	},
	computed: {
		...mapState(['userid'])
	},
	methods: {
		// 点击tab
		clickTab: function(e) {
		  let num = e.currentTarget.dataset.current
		  if (this.currentTab == num) {
		    return
		  }
		  this.currentTab = num
		},
		showCheckBtn() {
		  this.showCheck = false
		},
		// 取消
		cancelBtn() {
		  this.booklist.forEach((item, index) => {
		    if (item.checked) {
		      item.checked = false
		    }
		  })
		  this.showCheck = true
		},
		// 选中item
		selectedItem(e) {
		  if (this.showCheck) {
		    let bId = e.currentTarget.dataset.item.book_id
		    let cId = e.currentTarget.dataset.item.read_chapter_id
		    uni.setStorageSync('__bookid__', bId)
		    uni.setStorageSync('__sectionid__', cId)
		    uni.navigateTo({
		      url: '../read/read'
		    })
		  } else {
		    let selectItem = e.currentTarget.dataset.item
		    this.booklist.forEach((item, index) => {
		      if (item.book_id === selectItem.book_id) {
		        item.checked = !item.checked
		      }
		    })
		  }
		},
		// 删除
		deleteBook() {
		  let selectId = ''
		  // let booklist = this.booklist
		  this.booklist.forEach((item, index) => {
		    if (item.checked) {
		      selectId += item.book_id + ','
		    }
		  })
		  selectId = selectId.slice(0, -1)
		  if (selectId) {
		    let para = {
		      user_id: this.userId,
		      book_id: selectId
		    }
		    api.post('/user/deleteUserCase', para).then(res => {
		      if (res.code === 200) {
		        selectId = ''
		        this.showCheck = false
		        this.getBookListData()
		      } else {
		        uni.showToast({
		          title: res.msg,
		          icon: 'none',
		          duration: 2000
		        })
		      }
		    })
		  } else {
		    uni.showToast({
		      title: '请选择需要删除的书籍',
		      icon: 'none',
		      duration: 1500
		    })
		  }
		},
		// 全选
		allCheckBtn() {
		  this.booklist.forEach((item, index) => {
		    if (!item.checked) {
		      item.checked = true
		    }
		  })
		},
		selectedReadItem(e) {
		  let bId = e.currentTarget.dataset.item.book_id
		  let cId = e.currentTarget.dataset.item.read_chapter_id
		  uni.setStorageSync('__bookid__', bId)
		  uni.setStorageSync('__sectionid__', cId)
		  uni.navigateTo({
		    url: '../read/read'
		  })
		},
		clearReadList() {
		  let allBookId = ''
		  this.readlist.forEach((item, index) => {
		    allBookId += item.book_id + ','
		  })
		  allBookId = allBookId.slice(0, -1)
		  if (allBookId) {
		    let para = {
		      user_id: this.userId,
		      book_id: allBookId
		    }
		    api.post('/user/deleteUserRead', para).then(res => {
		      if (res.code === 200) {
						this.readlist = []
		      } else {
		        uni.showToast({
		          title: '清空失败',
		          icon: 'none',
		          duration: 2000
		        })
		      }
		    })
		  }
		},
		// 跳转到书籍详情页
		goBookDetails(e) {
		  let id = e.currentTarget.dataset.id
		  uni.navigateTo({
		    url: `../book/book?id=${id}`
		  })
		},
		gotoHome() {
		  uni.switchTab({
		    url: '../index/index'
		  })
		},
		getBookListData() {
		  let para = {
		    user_id: this.userId
		  }
		  api.post('/user/bookCaseList', para).then(res => {
		    if (res.code === 200) {
		      let list = res.data
		      list.forEach((item, index) => {
		        item.checked = false
		      })
		      this.booklist = list
		    } else {
		      this.booklist = []
		    }
		  })
		},
		getReadList() {
		  let para = {
		    user_id: this.userId
		  }
		  api.post('/user/readBookList', para).then(res => {
		    if (res.code === 200) {
		      this.readlist = res.data
		    } else {
					this.readlist = []
		    }
		  })
		}
		
	}
}	
